body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

:root {
  --color-active: #00acdc;
}

.header {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  box-shadow: 0 2px 4px #0000000d;
}

.header__menu {
  margin: 0 50px;
}

.menu {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.menu__logo {
  flex-grow: 1;
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list-item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #000;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link_blue, .nav__link:hover {
  color: var(--color-active);
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color-active);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}
